










































































































































import countryList from 'country-list';
import { Component, Vue } from 'vue-property-decorator';
import { Form } from 'element-ui';

import {
  getInternships,
  updateInternship,
  deleteInternship,
  defaultInternshipData,
  publishInternship,
} from '../../../api/internships';
import {
  IInternshipEntity,
  InternshipOpts,
  INTERNSHIP_MODE,
} from '../../../declarations';

import Pagination from '../../../components/Pagination/index.vue';
import CrudBtn from '../../../components/CrudBtn/index.vue';
import EditInternship from '../dialog/EditInternship.vue';

import { CategoriesModule } from '../../../store/modules/categories';

@Component({
  name: 'InternshipsStudentList',
  components: {
    Pagination,
    EditInternship,
    CrudBtn,
  },
})
export default class extends Vue {
  private tableKey = 0;
  private list: IInternshipEntity[] = [];
  private total = 0;

  private listLoading = true;
  private listQuery: InternshipOpts = {
    page: 1,
    limit: 10,
    subject: undefined,
    mode: [INTERNSHIP_MODE.WAITING],
    countries: [],
    types: [],
    isAbroad: false,
  };

  private countryList = countryList.getNames();

  private get types() {
    return CategoriesModule.categories;
  }

  public created() {
    this.getList();
  }

  private getList() {
    this.listLoading = true;
    getInternships(this.listQuery).then((res: any) => {
      this.list = res ? res.data : [];
      this.total = res ? res.max : 0;
      this.listLoading = false;
    });
  }

  private handleFilter() {
    this.getList();
  }

  private async handlePublish(row: IInternshipEntity) {
    await publishInternship(row.id!);
    this.getList();
    this.$notify({
      title: this.$t('notify.internships.publish.title') as string,
      message: this.$t('notify.internships.publish.msg') as string,
      type: 'success',
      duration: 2000,
    });
  }

  private handleUpdate(row: IInternshipEntity) {
    (this.$refs.EditInternship as EditInternship)
      .update(row)
      .then(async updatedRow => {
        if (updatedRow) {
          await updateInternship(updatedRow.id!, updatedRow);
          this.getList();
          this.$notify({
            title: this.$t('notify.internship.update.title') as string,
            message: this.$t('notify.internship.update.msg') as string,
            type: 'success',
            duration: 2000,
          });
        }
      });
  }

  private async handleDelete(row: IInternshipEntity) {
    await deleteInternship(row.id!);
    this.getList();
    this.$notify({
      title: this.$t('notify.internships.delete.title') as string,
      message: this.$t('notify.internships.delete.msg') as string,
      type: 'success',
      duration: 2000,
    });
  }
}
